* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Manrope", sans-serif;
}

#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100vh;
  background: #1f2531;
  color: #c2d0dd;
}

.container {
  background: #313a49;
  width: 60%;
  height: 45%;
  border-radius: 25px;
  padding: 20px;
  text-align: center;
}

.container .id-advice .id {
  width: 100%;
  text-align: center;
  font-size: 8px;
  color: #64d7a6;
}

.container .id-advice .id::before {
  content: "ADVICE #";
  margin-right: 5px;
}

.container .id-advice div {
  margin-top: 10px;
  height: 60px;
}

.container .id-advice div p {
  font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}

.container .id-advice div ::before {
  content: '"';
}

.container .id-advice div ::after {
  content: '"';
}

.container .divider-button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.container .divider-button .pattern-divider {
  position: relative;
  top: -10px;
  height: 80px;
  color: white;
}

.container .divider-button button {
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  border: none;
  background: #54feab;
  position: relative;
  top: -28px;
}

.container .divider-button button .dice {
  width: 12px;
  height: 12px;
  background: white;
}
